:root {
    --font-size: 14px;
}

.load {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: #000;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 300;
    top: 0;
}

.home-main-con {
    display: flex;
    width: 975px;
    /* max-width: 975px; */
    justify-content: flex-start;
    margin: 0 auto;
    margin-top: 90px;
    position: relative;
}

.left-con {
    width: 65%;
    z-index: 200;
}

.right-con {
    position: fixed;
    width: 100%;
    max-width: 975px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding-top: 20px;
}

.right-con-in {
    margin-right: 10px;
}

.right-profile-con {
    display: flex;
    align-items: center;
}

.right-profile-con>img {
    border-radius: 50%;
    width: 60px;
    height: 60px;
}

.right-profile-title {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 20px;
}

#main-t {
    color: #fff;
}

.right-profile-title span {
    margin: 2px 0;
}

.btn-sub {
    margin-left: 150px;
    font-size: var(--font-size);
    color: #20a7ff;
    cursor: pointer;
}

.story {
    width: 100%;
    border: 1px solid rgb(140, 130, 115);
    height: auto;
    border-radius: 0.25rem;
    display: flex;
    overflow-x: auto;
    flex-wrap: nowrap;
    margin-bottom: 30px;
}

.story::-webkit-scrollbar {
    display: none;
}

.story-in {
    display: flex;
    list-style: none;
    padding: 0;
}

.story-in>li {
    margin: 0 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 12px;
    color: rgb(209, 209, 209);
}

.icon-con {
    border-radius: 50%;
    background: linear-gradient(240deg, rgb(156, 56, 156), rgb(255, 99, 88));
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 7px;
}

.icon-con img {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    object-fit: cover;
    border: 3.5px solid #000;
    ;
}

.icon-empty {
    margin: 0 8px;
}

.col-rec {
    font-size: var(--font-size);
    font-weight: 600;
    margin-top: 20px;
    display: flex;
}

#all {
    margin-left: 190px;
    font-size: 12.5px;
    color: #fff;
}

.post-con {
    width: 100%;
    border: 1px solid rgb(140, 130, 115);
    margin-bottom: 30px;
    border-top-left-radius: 0.15rem;
    border-top-right-radius: 0.15rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.post-con>img {
    width: 100%;
    height: 100%;
}

.post-top {
    width: 100%;
    display: flex;
    align-items: center;
    padding: 10px 15px;
    justify-content: space-between;
    border-bottom: 1px solid rgb(140, 130, 115);
}

.post-top-left {
    display: flex;
    align-items: center;
    font-size: var(--font-size);
    font-weight: 600;
    color: rgb(218, 218, 218);
}

.post-top-left img {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    margin-right: 15px;
}

.post-option {
    display: flex;
    align-items: center;
    height: 50px;
    padding: 0 15px;
    justify-content: space-between;
}

.post-like {
    height: auto;
    padding: 0px 15px;
    font-size: 13px;
    color: rgb(218, 218, 218);
    font-weight: 600;
}

.post-holder {
    margin-top: 10px;
    display: flex;
    padding: 0 15px;
    font-size: 13px;
}

.post-holder-name {
    font-weight: 600;
    color: rgb(218, 218, 218);
    display: flex;
    align-items: flex-end;
}

.post-holder-content {
    margin-left: 10px;
    color: rgb(218, 218, 218);
    display: flex;
    align-items: flex-end;
    font-size: 14.5px;
}

.post-holder-more {
    margin-left: 10px;
}

.post-time {
    margin: 7px 0;
    padding: 0 15px;
    font-size: 12px;
    font-weight: 600;
}

.post-comment {
    display: flex;
    align-items: center;
    padding: 20px 15px;
    border-top: 1px solid rgb(140, 130, 115);
    justify-content: space-between;
}

.post-comment-left {
    display: flex;
    align-items: center;
}

.post-comment-inner {
    font-size: 14px;
    margin-left: 20px;
    display: flex;
    align-items: center;
    cursor: text;
}

.post-comment-inner>span {
    height: auto;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.post-comment-right {
    font-size: 14px;
    color: #20a7ff;
    opacity: .6;
}

.option-left {
    display: flex;
}

.option-left svg {
    cursor: pointer;
    margin-right: 15px;
}

.iconify {
    color: #fff;
}

#save:hover {
    opacity: 0.6;
}

@media screen and (max-width:1000px) {
    .home-main-con {
        width: 614px;
    }
    .left-con {
        width: 100%;
    }
    .right-con {
        display: none;
    }
}

@media screen and (max-width:614px) {
    .story-in {
        margin-bottom: 0;
    }
    .post-top {
        border: 0;
    }
    .post-con {
        margin-top: 0;
        border: 0;
    }
    body {
        background: #000;
    }
    .story {
        border: 0;
        border-bottom: 1.5px solid rgb(27, 27, 27);
        height: auto;
        padding-bottom: 10px;
        margin: 0;
    }
    .home-main-con {
        width: 100%;
        margin-top: 50px;
    }
    .left-con {
        width: 100%;
    }
    .right-con {
        display: none;
    }
    .post-con {
        margin-bottom: 0;
    }
    .post-comment {
        display: none;
    }
    .icon-con {
        width: 79px;
        height: 79px;
    }
    .icon-con img {
        width: 75px;
        height: 75px;
    }
    #icon-empty {
        margin: 0 0;
        display: none;
    }
}